import React from "react";
import {Link, link} from "react-router-dom";

import './navbar.css'
import './font.css'

import logo from './images/logo.png'

function Navbar(){
    return(
        <div className="Nav">
            <div className="navbar-upper">
                <p><span> /</span> . 10/12, </p>
                <p><span> /</span>+995 596 33 33 00</p>
                <p><span>- /</span>sales@allianceconstruction.ge</p>
            </div>

            <div className="navbar-bottom">
                <div className="logo_div">
                    <img src={logo} />
                </div>

                <div className="navbar_menu">
                    <span></span>
                    <span> </span>
                    <span> </span>
                    <Link><span></span></Link>
                    <span></span>
                </div>

                <div className="language">
                    <Link></Link>
                    <Link></Link>
                </div>
            </div>
        </div>
    )
}

export default Navbar;